.workshops {
    padding-bottom: calc(var(--cell) * 2);
}

.workshops .workshops-items {
    padding-left: 240px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}


@media screen and (max-width: 1024px) {

    .workshops .workshops-items {
        padding-left: 140px;
    }
}

@media screen and (max-width: 764px) {
    .workshops .workshops-items {
        padding-left: 0px;
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .workshops .container-fluid {
        width: calc(100% - 90px);
    }
}


